import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { solid, brands } from '@fortawesome/fontawesome-svg-core/import.macro';
import styled from "styled-components";

const StyledFooter = styled.footer`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  background: #202020;
  height: 100px;
`;

const StyledAnchor = styled.a`
  position: relative;
  cursor: pointer;

  :hover .popup {
    display: block;
  }

  .popup {
    display: none;
    position: absolute;
    bottom: 100%;
    background: #454545;
    color: #fff;
    padding: .25rem .5rem;
    text-decoration: none;
    left: 50%;
    transform: translateX(-50%);
    border-radius: .25rem;

    :after {
      content: "";
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      height: 0;
      width: 0;
      border: 10px solid transparent;
      border-top-color: #454545;
    }
  }
`;

const Footer = () => {
  return (
    <StyledFooter>
      <StyledAnchor href="https://github.com/Geefi-23" target="_blank">
        <FontAwesomeIcon className="text-white" icon={brands('github')} size="2xl" />
        <span className="popup">Github</span>
      </StyledAnchor>
      <StyledAnchor href="https://www.linkedin.com/in/milton-jos%C3%A9-280549244" target="_blank">
        <FontAwesomeIcon className="text-white" icon={brands('linkedin')} size="2xl" />
        <span className="popup">Linkedin</span>
      </StyledAnchor>
    </StyledFooter>
  );
};

export default Footer;