import { ReactComponent as ProgressiveApp } from '../svg/progressive_app.svg';
import tryRequire from '../tryRequire';
import styled from "styled-components";
import projects from '../projects.json';

const Section = styled.section`
  min-height: 500px;
  padding: 2rem 0;

  :target {
    scroll-margin-top: 100px;
  }
`;

const SectionTitle = styled.h4`
  text-align: center;
  font-weight: 700;
`;

const StackCard = styled.div`
  background: #fff;
  height: 160px;
  width: 160px;
  border-radius: .25rem;
  padding: 1rem;

  img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }
`;

const ProjectCard = styled.article`
  background: #fff;
  height: 325px;
  min-width: 250px;
  max-width: 290px;
  flex: 1;
  border-radius: .25rem;
  padding: .5rem;
`;

const ContactForm = styled.form`
  display: flex;
  flex-direction: column;
  width: 600px;
  gap: 1rem;

  input, textarea {
    border: 1px solid #202020;
    border-radius: 0;
    padding: .5rem;
  }

  input {
    width: 50%;
  }

  textarea {
    resize: none;
  }

  button[type="submit"] {
    background: #202020;
    color: #fff;
    height: 35px;
    width: 25%;
    border: none;
  }
`;

const ResponsiveContainer = styled.div`
  @media screen and (max-width: 400px) {
    flex-direction: column;

    svg {
      width: 100%;
    }
  }
`;

const Home = () => {

  const handleContactForm = async (evt) => {
    evt.preventDefault();
    
    const form = evt.target;
    const data = {
      email: form.email.value,
      message: form.message.value
    };
    const init = {
      method: 'post',
      body: JSON.stringify(data),
      headers: {
        'Content-Type': 'application/json'
      }
    };

    const res = await (await fetch('https://localhost:5000/contact', init)).json();

    console.log(res);
  };

  console.log(tryRequire)
  return (
    <main>
      <Section className="d-flex align-items-center" style={{ background: '#262626' }}>
        <ResponsiveContainer className="container d-flex align-items-center justify-content-between">
          <div className="text-white">
            <h4 className="fw-normal m-0">Eu sou o <strong>Milton</strong></h4>
            <h5 className="fw-normal mb-4">Desenvolvedor Full-Stack</h5>
            <h3>Bem vindo ao meu portfólio!</h3>
          </div>
          <ProgressiveApp width="500" height="256" />
        </ResponsiveContainer>
      </Section>
      <Section id="sobre">
        <SectionTitle>Sobre mim</SectionTitle>
        <div className='container'>
          <ResponsiveContainer className="d-flex justify-content-center align-items-center mt-5">
            <div 
              className="bg-dark rounded-circle"
              style={{
                flexShrink: 0,
                height: '200px',
                width: '200px',
                background: `url(${require('../images/profile.jpg')}) center center / contain`
              }} 
            />
            <p className="m-0 ms-4 mt-3" style={{ maxWidth: '600px' }}>
              Olá, visitante! Meu nome é Milton, tenho 17 anos e sou desenvolvedor Full-Stack. Meu interesse em tecnologia
              vem desde a infância, época em que eu desmontava e remontava meus brinquedos só pra ver como eles funcionavam por dentro.
              Conhecido como um ótimo líder, alguém que sabe se comunicar, se organizar e também improvisar, sou um autodidata nato.
              Meu foco é atender as demandas dos meus clientes com rapidez e qualidade, tanto em design UI/UX, 
              quanto na escrita dos meus códigos.
            </p>

          </ResponsiveContainer>
        </div>
      </Section>
      <Section id="stacks" style={{ background: '#262626' }}>
        <SectionTitle className="text-white">Stacks</SectionTitle>
        <div className="container">
          <div className="d-flex gap-4 flex-wrap justify-content-center mx-auto mt-5" style={{ maxWidth: '712px' }}>
            {
              [
                'HTML5.png',
                'CSS3.png',
                'JAVASCRIPT.png',
                'REACTJS.png',
                'NODEJS.png',
                'GIT.png',
                'PHP.png',
                'LARAVEL.svg',
              ].map((image) => (
                <StackCard>
                  <img src={require('../images/logos/'+image)} />
                </StackCard>
              ))
            }
            
          </div>
        </div>
      </Section>
      <Section id="projetos" style={{ background: '#333' }}>
        <SectionTitle className="text-white">Projetos</SectionTitle>
        <div className="container mt-5 d-flex justify-content-center flex-wrap gap-5">
          {
            projects?.map(project => (
              <ProjectCard>
                <div 
                className="bg-light rounded-top" 
                style={{ 
                  background: `url(${project?.preview && require('../images/projects/'+project?.preview)})`,
                  height: '125px' 
                }} 
                />
                <h5 className="fw-bold mt-3">{project?.title}</h5>
                <p>{project?.description}</p>
              </ProjectCard>
            ))
          }

        </div>
      </Section>
      <Section id="contato">
        <SectionTitle>Entre em contato</SectionTitle>
        <div className="container d-flex justify-content-center mt-5">
          <ContactForm onSubmit={handleContactForm}>
            <input name="email" type="text" placeholder="Seu email" autoComplete='off' />
            <textarea name="message" rows={7} placeholder="Escreva uma mensagem" />
            <button type="submit">Enviar</button>
          </ContactForm>
        </div>
      </Section>
    </main>
  );
};

export default Home;